@charset "UTF-8";
@import "variables";
@import "../../../sass/func";

button {
  border: 0;
  padding: 0;

  outline: none;
}
.content-download {
    background: url("../images/download/bg.png") no-repeat top;
    background-color: #f5f5f5;
    background-size: pxToRem(750px) pxToRem(218px);
    padding: pxToRem(144px) 0 pxToRem(34px);
    .logo-download {
        @include background("../images/download/logo.png",
        276px,
        278px);
        margin: 0 auto;
    }
    .text {
        @include background("../images/download/text.png",
        580px,
        56px);
        margin: pxToRem(30px) auto pxToRem(60px);
    }
    .box {
        @include background("../images/download/box.png",
        750px,
        554px);
        margin: pxToRem(100px) auto pxToRem(30px);
    }
    .icon-download {
      @include inline-background("data:img/jpg;base64,iVBORw0KGgoAAAANSUhEUgAAABcAAAAcBAMAAAB4wzlQAAAAGFBMVEUAAAD///////////////////////////8jfp1fAAAAB3RSTlMAYHhwIII45vfqJwAAAFRJREFUGNO9ySEOhAAQQ9FmxWq4AQ6LwyLmCCRcg2Dm+nzVKoLjqzZPtDYtjM9OVR1Ne9Wmud2kn/clQQZBAchgAkKACQgBJiAEuL/eGtOgTufzuQHpBkduY9CQIwAAAABJRU5ErkJggg==", 24px, 28px);
      margin-right: pxToRem(12px);
      vertical-align: text-bottom;
    }
    .btn {
        display: block;

        margin: 0 auto;
        border-radius: pxToRem(4px);
        width: pxToRem(390px);
        height: pxToRem(88px);
        border-radius: pxToRem(88px);
        font-size: pxToRem(32px);

        color: #fff;
        background: linear-gradient(to right, #faa700, #ffbf00);

    }
}
